* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {list-style: none;}

.list {
  background-color: #ffd8d8;
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  padding: 30px 0;
  position: relative;
}
.list h2 {
  font-size: 30px;
  color: #ff1d73;
  text-align: center;
  margin-bottom: 30px;
}
.list form {
  width: 90%;
  height: 600px;
  border: 2px solid #ff1d73;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  padding: 30px 0;
}
.list form > input {
  width: 60%;
  height: 40px;
  border: 2px solid #ff1d73;
  background-color: #ffecec;
  padding: 0 10px;
  font-size: 16px;
  color: #ff1d73;
  text-decoration: none;
}
.list form input::placeholder {
  color: #ff1d73;
  font-weight: bold;
}
.list form button {
  width: 20%;
  height: 40px;
  border: 0;
  background-color: #ff1d73;
  font-size: 16px;
}
ul {
  width: 70%;
  height: 460px;
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
}
ul li {
  color: #ff1d73;
  font-size: 20px;
  font-weight: bold;
  position: relative;
  padding-left: 20px;
}